<template>
  <v-app id="inspire">
    <v-app-bar
      app
      clipped-right
      flat
      src="../src/assets/rainbow-colors-background.jpg"
    >
      <v-toolbar-title
        style="color:#fff; cursor: pointer;"
        @click="$router.push('/')"
      >
        Korean Rainbow United
      </v-toolbar-title>

      <v-spacer></v-spacer>
      <v-toolbar-items
        v-show="!isMobile"
        d-flex
        v-for="item in items"
        :key="item.title"
      >
        <v-btn color="#fff" :to="item.to" id="logo" text depressed>{{
          item.title
        }}</v-btn>
      </v-toolbar-items>

      <v-spacer></v-spacer>
      <v-app-bar-nav-icon
        color="#fff"
        v-show="isMobile"
        @click="drawer = !drawer"
      ></v-app-bar-nav-icon>
    </v-app-bar>

    <v-navigation-drawer
      v-model="drawer"
      app
      clipped
      right
      v-show="isMobile"
      class="d-flex d-sm-none"
    >
      <v-list>
        <v-list-item v-for="item in items" :key="item.title" :to="item.to" link>
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-container fluid class="mt-15">
      <!-- Provides the application the proper gutter -->
      <!-- If using vue-router -->
      <router-view></router-view>
    </v-container>

    <v-footer app dark padless class="mt-10">
      <v-card flat tile class="white--text text-center">
        <v-card-text>
          <v-btn
            v-for="icon in icons"
            :key="icon"
            class="mx-4 white--text"
            icon
          >
            <v-icon size="24px">
              {{ icon }}
            </v-icon>
          </v-btn>
        </v-card-text>

        <v-card-text class="white--text pt-0">
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Totam
          molestiae, sapiente harum minima, reprehenderit ea dolorem obcaecati
          possimus blanditiis officia, porro in fugiat. Error reprehenderit
          explicabo temporibus qui ea eos!
        </v-card-text>

        <v-card-text class="white--text">
          {{ new Date().getFullYear() }} —
          <strong>Korean Rainbow United</strong>
        </v-card-text>
      </v-card>
    </v-footer>
  </v-app>
</template>

<script>
import Footer from './components/Footer';

export default {
  name: 'App',

  components: { Footer },

  data: () => ({
    isMobile: false,
    drawer: null,
    items: [
      { title: 'About Us', icon: 'mdi-calendar', to: '/about' },
      { title: 'News', icon: 'mdi-lead-pencil', to: '/news' },
      { title: 'Contact Us', icon: 'mdi-book-open-variant', to: '/contact' },
    ],
    icons: ['mdi-facebook', 'mdi-twitter', 'mdi-linkedin', 'mdi-instagram'],
  }),

  beforeDestroy() {
    if (typeof window === 'undefined') return;

    window.removeEventListener('resize', this.onResize, { passive: true });
  },

  mounted() {
    this.onResize();

    window.addEventListener('resize', this.onResize, { passive: true });
  },

  methods: {
    onResize() {
      this.isMobile = window.innerWidth < 600;
    },
  },
};
</script>

